.pat-tabs {
  text-align: left;
  line-height: 2.9;
  height: 2.8em;
  // overflow: hidden;
  border-bottom: #0081be 1px solid;
  margin-bottom: 2em;
  transition: line-height .5s, opacity .5s, height .5s, font-size .5s;
  position: relative;

  &.injecting {
    opacity: 1;

    &:before {
      display: none;
    }
  }

  &.bumped {
    // opacity: 0.9;
    line-height: 2.1em;
    height: 2em;
    font-size: 0.8em;

    &.sticky-supported {
      @media screen and (-webkit-min-device-pixel-ratio:0) {
        position: -webkit-sticky !important;
        position: sticky !important;
        top: 0.01em !important;
      }
    }
  }

  ul {
    text-align: center;
    }

  > a,
  > nav > a,
  .touchevents & .extra-tabs a {
    display: inline-block;
    text-decoration: none !important;
    padding: 0 1.5em 0 0;
    @include screen-size(small) {
      padding: 0 0.5em;
    }
    position: relative;
    color: black;
    // float: left;

    .counter {
      background-color: rgba(0,0,0,0.15);
      color: black;
      padding: 0 0.5em;
      font-size: 0.6em;
      font-weight: bold;
      line-height: 1.65;
      border-radius: 20px;
      display: inline-block;
      min-width: 1.6em;
      text-align: center;
      box-sizing: border-box;
      position: relative;
      top: -0.38em;
    }

    &.risk-identified {
      .counter {
        color: white;
        background-color: red;
      }
    }

    &.postponed {
      .counter {
        color: black;
        background-color: orange;
      }
    }

    &.current {
      cursor: default;
    }

    &.injecting:before {
      display: none;
    }

    sup {
      font-size: 0.6em;
      color: gray;
      font-weight: bold;
    }

    .hovermq & {
      &:not(.title):not(.organisation):not(.current):hover:after {
        position: absolute;
        content: " ";
        bottom: 1px;
        left: 0;
        right: 0;
        opacity: 0.6;
        border-bottom: 0.3em solid rgba(255,255,255,0.5);
      }
    }

    &.current:after {
      position: absolute;
      content: " ";
      bottom: 1px;
      left: 0;
      right: 1.5em;
      border-bottom: 0.3em solid $colour-accent;
    }

  }

  .no-hovermq & {
    overflow-x: auto;
    overflow-y: visible;
    -webkit-overflow-scrolling: touch;

    margin: 0 -1 * $small-screen-padding 1em;
    width: calc(100% + #{$small-screen-padding} + #{$small-screen-padding});

    white-space: nowrap;

    .extra-tabs {
      padding-right: $small-screen-padding;
      display: inline-block;
      color: black;
    }

    > a,
    > label {
      float: none;
      display: inline-block;
      margin: 0;

      &:first-child {
        margin-left: $small-screen-padding;
      }
    }
  }

  .hovermq & {

    span.extra-tabs {
      border: 1px transparent solid;
      padding-top: 1.5em;
      display: inline-block;
      float: right;
      height: 0;
      width: 1.5em;
      overflow: hidden;
      position: absolute;
      right: 0;
      top: 0.6em;
      z-index: 1;

      -webkit-transition: all 0.1s;

      &:before {
        font-family: fontello;
        content: '\e87b';
        line-height: 1.5em;
        text-align: center;
        position: absolute;
        right: 0;
        top: 0;
        height: 1.5em;
        width: 1.5em;
        color: rgba(0,0,0,0.5);
      }

      &:hover {
        border: 1px #dedede solid;
        padding-bottom: 0.5em;
        background-color: #f1f1f1;

        &:before {
          color: black;
        }
        height: auto;
        width: 10em;
      }

      a {
        display: block;
        float: none;
        padding: 0.4em 0.7em;
        line-height: 1.5;
        text-align: left;
        color: black;

        &:hover {
          background-color: rgba(0,0,0,0.05);
          text-decoration: none;
        }
      }
    }

  }
}